.request__button {
  position: relative;
  left: -10px;
  min-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  cursor: pointer;
}
.request__button .request__text {
  position: relative;
  z-index: 2;
  color: var(--color_white);
  font-size: 12px;
  font-weight: 700;
  line-height: 150%;
  white-space: nowrap;
}
.request__button .request__bg {
  position: absolute;
  right: 10px;
  top: 0;
  width: calc(100% - 20px);
  min-height: 60px;
  background-color: var(--color_brown);
  transform: skewX(-17deg);
}
.request__button .request__line {
  position: absolute;
  right: 0;
  top: 0;
  width: 4px;
  min-height: 60px;
  background-color: var(--color_white);
  transform: skewX(-17deg);
}

