:root {
  --color_white: #fff;
  --color_black: #171717;
  --color_dark_bg: #131313;
  --color_brown: #c2ab81;
  --color_grey: #666;
  --color_font_stroke: #808080;
  --montserrat: "Montserrat", sans-serif;
  --mulish: "Mulish", sans-serif;
  --transition: all 0.2s ease-out;
}

/*===== global styles =====*/
* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: var(--color_white);
  background: var(--color_dark_bg);
  font-family: var(--montserrat);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1920px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 100px;
}
@media only screen and (max-width: 1700px) {
  .container {
    padding: 0 80px;
  }
}
@media (max-width: 1599px) {
  .container {
    padding: 0 60px;
  }
}
@media (max-width: 1279px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 575px) {
  .container {
    padding: 0 15px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  font-family: var(--montserrat);
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.header {
  padding: 28px 0;
}
@media (max-width: 575px) {
  .header {
    padding: 15px 0 18px;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1599px) {
  .header__wrapper {
    padding: 0 12px;
  }
}
@media (max-width: 575px) {
  .header__wrapper {
    padding: 0 0 0 3px;
  }
}

.header__panel {
  flex-grow: 0;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: 94px;
  margin-right: 76px;
}
@media (max-width: 1599px) {
  .header__panel {
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .header__panel {
    flex-grow: 1;
    justify-content: space-between;
  }
}

.header__burger {
  position: relative;
  display: none;
  width: 31px;
  height: 19px;
  cursor: pointer;
}
@media (max-width: 1599px) {
  .header__burger {
    display: block;
  }
}
.header__burger span, .header__burger::before, .header__burger::after {
  position: absolute;
  left: 0;
  height: 3px;
  background-color: var(--color_white);
  border-radius: 3px;
}
.header__burger span {
  top: 8px;
  width: 100%;
}
.header__burger::before {
  content: "";
  top: 0;
  width: 16px;
}
.header__burger::after {
  content: "";
  bottom: 0;
  width: 22px;
}

.header__logo {
  display: block;
  width: 156px;
  height: 60px;
}
@media (max-width: 575px) {
  .header__logo {
    width: 112px;
    height: 38px;
  }
}

.header__menu {
  flex-grow: 1;
}
@media (max-width: 1599px) {
  .header__menu {
    display: none;
  }
}

.header__list::after {
  content: "";
  clear: both;
}
.header__list li {
  position: relative;
  float: left;
  margin-right: 42px;
}
@media only screen and (max-width: 1700px) {
  .header__list li {
    margin-right: 32px;
  }
}
@media (min-width: 1600px) {
  .header__list li::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--color_white);
    transform: scale(0.9);
    opacity: 0;
    transition: var(--transition);
  }
}
.header__list li:hover:before {
  transform: scale(1) translateY(4px);
  opacity: 1;
}
.header__list a {
  font-size: 14px;
  font-weight: 700;
  line-height: 150%;
  white-space: nowrap;
}

.header__feedback {
  display: none;
}

.header__buttons {
  display: flex;
  align-items: center;
  gap: 34px;
}
@media (max-width: 1023px) {
  .header__buttons {
    position: relative;
    top: 2px;
    gap: 7px;
  }
}
@media (max-width: 767px) {
  .header__buttons {
    display: none;
  }
}

.header__phone {
  position: relative;
  width: 74px;
  min-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__phone span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 60px;
  border: 1px solid var(--color_brown);
  transform: skewX(-17deg);
}

.header__button.request__button {
  padding: 10px 78px;
}
@media (max-width: 1023px) {
  .header__button.request__button {
    padding: 10px 38px;
  }
}

.main {
  flex-grow: 1;
}